import { useState } from 'react'
import '../css/SideMenu.css'

function SideMenu (props) {

	let [toggle, toggleBar] = useState(false);
	let show = toggle? "":"-translate-x-full";

	return <div className="sticky top-0">
		<nav className="bg-gradient-to-r from-green-400 to-blue-500 flex justify-between md:hidden">
			<button className="p-3">Home</button>
			<button className="p-3" onClick={()=>toggleBar(!toggle)}>
				<svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
  			<path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
			</svg></button>
		</nav>
		<nav className={"bg-gradient-to-b from-green-400 to-blue-500 w-64 h-screen absolute md:relative inset-y-0 left-0 transform " + show + " md:-translate-x-0 transition duration-200 ease-in-out"}>
			<div className="h-40 p-5">
				<div className="rounded-full h-24 w-24 flex items-center justify-center bg-white bg-opacity-25 m-auto">Foto</div>
				</div>
				{props.items.map((item, index) => <MenuItem key={index} title={item.title} active={item.active === index} />)}
		</nav>
	</div>
}

function MenuItem (props) {
	
	let active = props.active
	let style = `block py-2.5 px-4`
	let activeStyle = active?"w-full bg-white bg-opacity-25 backdrop-filter backdrop-blur":"";
	let transition = "transition-colors duration-500 ease-in-out"

	return <div className={activeStyle + " "+ transition}>
			<a href={"#"+props.title} className={style}>{props.title}</a>	
	</div>
}

export {
	SideMenu,
	MenuItem
}