import './App.css';
// import {ButtonSuccess} from './jsx/Button';
import { SideMenu } from './jsx/SideMenu';
import { useState} from 'react';


function Section(prop) {
  return <section id={prop.section.title} className="h-screen p-4">{prop.section.title}</section>
}

function App() {

  const [index, setActive] = useState(0);
  const [offset, setOffset] = useState(0);
  //console.log(offset);

  let sections = [
    {
      title: 'Home',
      active: index
    },
    {
      title: 'Resume',
      active: index
    },
    {
      title: 'Blog',
      active: index
    },
    {
      title: 'Contact',
      active: index
    }
  ];

  const scroll = (event) => {
    let {scrollHeight, scrollTop} = event.target;
    let sectionSize = scrollHeight / sections.length;
    // scrollTop > offset = scrolling down else scrolling up
    let factor = scrollTop / sectionSize
    let index = scrollTop > offset ?
      Math.round(Math.floor(factor + 0.3)):
      Math.round(Math.ceil(factor - 0.3));
    //let index = Math.round(Math.floor(factor + 0.1))
    //event.target.scrollTop = (index * sectionSize);
    setActive(index);
    setOffset(scrollTop);
  }

  return (
    <div className="md:relative md:h-screen md:flex">
      {/* <div className="sticky top-0 w-full h-14 bg-green-800"></div> */}
      <SideMenu items={sections}/>
      <div className="md:overflow-y-scroll w-full smooth-scroll" onScroll={scroll}>
        {sections.map((section) => <Section key={section.title} section={section}/>)}
      </div>
    </div>
  );
}

export default App;
